import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-zippy',
  templateUrl: './zippy.component.html',
  styleUrls: ['./zippy.component.css']
})
export class ZippyComponent implements OnInit {

@Input('isShipping') isShipping: boolean = false;
@Input('isBilling') isBilling: boolean = false;

ShippingClicked(){
this.isShipping = !this.isShipping;
console.log("Shipping Clicked :" + this.isShipping )
//return this.isShipping;
}

BillingClicked(){
  this.isBilling = !this.isBilling;
  console.log("Billing Clicked :" + this.isBilling )
}
  constructor() { }

  ngOnInit() {
  }

}



